class Fun{
	webSite = "https://www.fangpaiwang.com";
	successData = new Array();
	errData = new Array();
	userInfo = "userInfo";//key
	likeHouse = "likeHouse";//
	likeEstate = "likeEstate";//
	historyHouse = "historyHouse";//
	houseKeys = "houseKeys";//ID
	historyEstate = "historyEstate";//
	estateKeys = "estateKeys";//ID
	searchList = "searchList";//key
	searchEstate = "searchEstate";//key
	price = "price";// 
	area = "area";// 
	room = "room";// 
	houseProperty = "houseProperty"; //  
	areaData = "areaData"; //  
	levelData = "levelData" //  
	statusData = "statusData" //  
	HomeMenu = "HomeMenu" //
	auction = "auction" // 
	listing = "y1" // 
	freeBuy = "freeBuy" // 

	navTo(url){// url
		uni.navigateTo({
			url:url
		})
	}
	redTo(url){// 
		uni.redirectTo({
			url:url
		})
	}
	
	getDate(dates) { //
		if (dates) {
			var date = new Date(dates * 1000);//10*1000131000
			var Y = date.getFullYear() + '-';
			var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
			var D = date.getDate() + ' ';
			var h = date.getHours() + ':';
			var m = (date.getMinutes() < 10 ? '0'+(date.getMinutes()) : date.getMinutes()) + ':';
			var s = (date.getSeconds() < 10 ? '0'+(date.getSeconds()) : date.getSeconds());
			return Y+M+D+h+m+s;
		} else {
			return "";
		}
	}
	
	getCurrenTime(){
		var myDate = new Date;
		return myDate.getFullYear()+""+myDate.getMonth()+1+""+myDate.getDate(); //
	}
	
	getReq(_url,_data="",_method='GET'){//get
		return new Promise((resolve,reject)=>{
			uni.request({
				url: _url, 
				method:_method,
				data:_data
			})
			.then((res)=>{
				resolve(res)
			})
			.catch((err)=>{
				// errData.errList(" ")
				reject(err)
			})
		})
	}
	showMsg(msg){
		uni.showToast({
			icon:'none',
			title:msg,
			duration:3000
		})
	}
	setStor(_key,_data,){
		uni.setStorage({
			key:_key,
			data:_data,
			success:function() {
				console.log('success');
			}
		})
	}
	getStor(_key){
		return uni.getStorage({
			key:_key,
			success:function(res){
				console.log("success",res)
				return res.data;
			},
			fail:function(res) {
				console.log("fail",res)
				return errData.push({code:3000});
			}
		})
	}
	
	getImgSrc(ImgSrc){
		if (ImgSrc == null || ImgSrc == "" ) {
			return "../../static/img/base/default.png";
		}
		if (ImgSrc.substr(0,4) == "http") {
			return ImgSrc;
		} else if (ImgSrc.substr(0,1) == "/") {
			return this.webSite+`${ImgSrc}`;
		} else {
			return this.webSite+`/${ImgSrc}`;
		}
	}
}

let fun = new Fun();
export default fun;